@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
.main-header {
  background-color: #ffffff;
  z-index: 1;
}

.main-header__wrapper-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 20px 15px;
}

.main-header__logo-cat:hover {
  opacity: 0.8;
}
.main-header__logo-cat:active {
  opacity: 0.6;
}

.main-header__logo-name:hover {
  opacity: 0.8;
}
.main-header__logo-name:active {
  opacity: 0.6;
}

@media (min-width: 768px) {
  .main-header {
    margin: 0 30px;
  }

  .main-header__logo-name {
    display: none;
  }

  .main-header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 24px;
  }

  .main-header__wrapper-links {
    padding: 0;
  }

  .main-header__cat-image {
    margin-right: 12px;
    width: 174px;
    height: 52px;
  }
}
@media (min-width: 1440px) {
  .main-header__wrapper {
    margin: 0 110px;
    position: absolute;
    background-color: transparent;
    top: 53px;
    left: 0;
    right: 0;
    padding: 0;
    z-index: 1;
  }

  .main-header__cat-image {
    margin-right: 14px;
    width: 202px;
    height: 59px;
  }
}
.page {
  height: 100%;
}

.page__body {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  background-color: #ffffff;
  color: #000000;
  margin: 0;
  position: relative;
}

* {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-nav {
  background-color: #ffffff;
}

.main-nav__toogle {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  width: 24px;
}

.main-nav__toogle--open .close-menu {
  display: block;
}
.main-nav__toogle--open .toogle-burger {
  display: none;
}

.main-nav__toogle--closed .close-menu {
  display: none;
}
.main-nav__toogle--closed .toogle-burger {
  display: block;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #ffffff;
}

.main-nav__item-link {
  text-decoration: none;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 1;
  color: #000000;
  padding: 22px 0 24px;
  text-transform: uppercase;
  display: block;
  position: relative;
}

.main-nav__toogle--nojs {
  visibility: hidden;
}

@media (max-width: 767px) {
  .main-nav__item-link {
    border-bottom: 1px solid #e6e6e6;
    text-align: center;
  }

  .main-nav__item:first-child .main-nav__item-link {
    border-top: 1px solid #e6e6e6;
  }

  .has-js .main-nav__wrapper--open .main-nav__list {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 1;
  }
  .has-js .main-nav__wrapper--closed .main-nav__list {
    display: none;
  }

  .main-nav__list {
    position: static;
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav {
    background-color: transparent;
  }

  .main-nav__wrapper {
    display: block;
  }

  .main-nav__toogle {
    display: none;
  }

  .main-nav__list {
    display: flex;
    gap: 35px;
  }

  .main-nav__item-link {
    padding: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
  .main-nav__item-link:hover {
    opacity: 0.6;
  }
  .main-nav__item-link:active {
    opacity: 0.3;
  }

  .main-nav__item-link--active::after {
    content: "";
    position: absolute;
    height: 2px;
    left: 0;
    right: 0;
    bottom: -10px;
    background-color: #68b738;
  }
}
@media (min-width: 1440px) {
  .main-nav__item-link--index {
    color: #ffffff;
  }
  .main-nav__item-link--index:hover {
    opacity: 0.6;
  }
  .main-nav__item-link--index:active {
    opacity: 0.3;
  }

  .main-nav__item:first-child .main-nav__item-link--index::after {
    content: "";
    position: absolute;
    height: 2px;
    left: 0;
    right: 0;
    bottom: -10px;
    background-color: #ffffff;
  }

  .main-nav__list {
    background-color: transparent;
  }

  .main-nav__item-link {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
  }
}
