.slider {
  margin: 0 auto;
  width: 280px;
  height: 256px;
  position: relative;
  display: flex;
}

.slider__before,
.slider__after {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  pointer-events: none;
  overflow: hidden;
}

.slider__image {
  height: 100%;
  object-fit: cover;
  width: 100%;
}

.slider__image--before {
  object-position: left;
}

.slider__image--after {
  object-position: right;
}

.slider__button {
  position: absolute;
  display: block;
  margin: 0 auto;
  padding: 0;
  height: 100%;
  width: 4px;
  border-radius: 2px;
  border: none;
  background-color: #666666;
  left: 50%;
  transform: translateX(-4px);
  cursor: pointer;
}

.slider__button:hover {
  background-color: #68b738;
}
.slider__button:hover .slider__button-scroller {
  border-color: #68b738;
}
.slider__button:hover .slider__button-scroller svg {
  fill: #68b738;
}

.slider__button:active {
  opacity: 0.6;
}

.slider__button-scroller {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #ffffff;
  border: 2px solid #666666;
}

@media (min-width: 768px) {
  .slider {
    height: 512px;
    width: 560px;
  }
}
@media (min-width: 1440px) {
  .slider {
    width: 560px;
    flex-shrink: 0;
  }
}
